                  >
                    <circle cx="12" cy="12" r="4"></circle>
                    <path d="M16 8v5a3 3 0 0 0 6 0v-1a10 10 0 1 0-3.92 7.94"></path>
                  </svg>
                  <input v-model="email" type="text" value="" placeholder="Email" />
                </div>
                <div id="password-field" class="field-wrapper input mb-2">
                  <svg
                    xmlns="http://www.w3.org/2000/svg"
                    width="24"
                    height="24"
                    viewBox="0 0 24 24"
                    fill="none"
                    stroke="currentColor"
                    stroke-width="2"
                    stroke-linecap="round"
                    stroke-linejoin="round"
                    class="feather feather-lock"
                  >
                    <rect x="3" y="11" width="18" height="11" rx="2" ry="2"></rect>
                    <path d="M7 11V7a5 5 0 0 1 10 0v4"></path>
                  </svg>
                  <input
                    v-model="password"
                    :type="password_show ? 'text' : 'password'"
                    value=""
                    placeholder="Password"
                  />
                </div>
                <div id="password-field" class="field-wrapper input mb-2">
                  <svg
                    xmlns="http://www.w3.org/2000/svg"
                    width="24"
                    height="24"
                    viewBox="0 0 24 24"
                    fill="none"
                    stroke="currentColor"
                    stroke-width="2"
                    stroke-linecap="round"
                    stroke-linejoin="round"
                    class="feather feather-lock"
                  >
                    <rect x="3" y="11" width="18" height="11" rx="2" ry="2"></rect>
                    <path d="M7 11V7a5 5 0 0 1 10 0v4"></path>
                  </svg>
                  <input
                    v-model="password_confirmation"
                    type="password"
                    value=""
                    placeholder="Confirm Password"
                  />
                </div>
                <div class="field-wrapper terms_condition">
                  <div class="n-chk new-checkbox checkbox-outline-primary">
                    <label class="new-control new-checkbox checkbox-outline-primary">
                      <input v-model="terms" type="checkbox" class="new-control-input" />
                      <span class="new-control-indicator"></span
                      ><span
                        >I agree to the
                        <a href="javascript:void(0);"> terms and conditions </a></span
                      >
                    </label>
                  </div>
                </div>
                <div class="d-sm-flex justify-content-between">
                  <div class="field-wrapper toggle-pass">
                    <p class="d-inline-block">Show Password</p>
                    <label class="switch s-primary">
                      <input v-model="password_show" type="checkbox" class="d-none" />
                      <span class="slider round"></span>
                    </label>
                  </div>
                  <div class="field-wrapper">
                    <button
                      type="submit"
                      @click.prevent="register"
                      class="btn btn-primary"
                      :disabled="!terms"
                    >
                      Get Started!
                    </button>
                  </div>
