<script>
import { registerUser, loginUser } from '@/utils/auth'

export default {
  name: 'register',
  data() {
    return {
      name: '',
      email: '',
      password: '',
      password_confirmation: '',
      terms: false,
      password_show: false,
    }
  },
  methods: {
    async register() {
      try {
        await registerUser(this.name, this.email, this.password, this.password_confirmation)
        await loginUser(this.email, this.password)

        this.$router.push('/')
      } catch (err) {
        alert(`Error: ${err}`)
      }
    },
  },
}
</script>
